#example-explore {
  background: #f3f4fa;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow-y: auto;
}
#explore-container {
  margin-left: 190px;
  padding: 10px 10px;
}
.example-list-panel {
  margin: 30px 15px 30px 15px;
}
.example-list-panel h3 {
    margin-bottom: 20px;
    border-bottom: 1px solid #ccd3e8;
    font-weight: normal;
    color: #252839;
}
.example-list-panel .chart-type-head span {
    font-size: 18px;
    padding-left: 5px;
    color: #999;
    font-weight: 200;
}
#left-container {
  position: fixed;
  top: 10px;
  bottom: 10px;
  left: 10px;
  width: 170px;
}
#left-toolbar {
  position: absolute;
  left: 0;
  right: 0;
  height: 30px;
  top: 0;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  padding-left: 5px;
  padding-top: 4px;
}
#left-toolbar .el-switch__label * {
    font-size: 12px;
    text-transform: uppercase;
}
#left-toolbar .el-switch__label.is-active {
    color: #181432;
}
#left-chart-nav {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 40px;
  background-color: #252839;
  overflow-y: hidden;
  border-radius: 10px;
  color: #fff;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
}
#left-chart-nav:hover {
    overflow-y: auto;
}
#left-chart-nav:hover a {
      text-decoration: none;
}
#left-chart-nav li {
    transition: 0.5s;
    cursor: pointer;
}
#left-chart-nav li a {
      height: 45px;
      padding: 10px 0 10px 10px;
      display: block;
      transition: 0.5s;
      text-decoration: none;
      color: inherit;
}
#left-chart-nav li a .chart-name {
        display: inline-block;
        position: relative;
        vertical-align: middle;
        margin-left: 10px;
}
#left-chart-nav li a .chart-icon {
        content: '';
        display: inline-block;
        width: 25px;
        height: 25px;
        background-image: url(../asset/chart-icon.png);
        background-size: 25px;
        background-repeat: no-repeat;
        border-radius: 50%;
        border: 1px solid #fff;
        vertical-align: middle;
}
#left-chart-nav li a.active {
        background-color: #e43c59;
}
#left-chart-nav li:hover {
      background-color: #11121a;
}
@media (max-width: 768px) {
#left-container {
    display: none;
}
#explore-container {
    margin-left: 0;
}
}
